.header {
  font-family: "JosefinSlab";
  width: 100%;
  height: 86px;
  box-shadow: 2px 3px 3px rgba(0, 0, 0, 0.31);
  opacity: 0.94;
  background-color: #fff;
  position: relative;
  z-index: 1000;

  &__inner {
    .wrapper();
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 86px;
  }

  &__logo {
    a {
      font-family: "HelveticaNeueBold";
      font-size: 38px;
      font-weight: 700;
      color: #626262;
      text-decoration: none;
      text-transform: uppercase;

      span {
        color: #06b5a9;
      }
    }
  }
}