.footer {
  width: 100%;
  background-color: #1b2330;

  &__inner {
    .wrapper();
    height: 104px;
    display: flex;
    justify-content: space-between;
    padding-top: 25px;
  }

  &__logo {
    font-family: "HelveticaNeueBold";
    font-weight: 700;
    font-size: 38px;
    color: #626262;
    line-height: 25.72px;
    text-decoration: none;
    text-transform: uppercase;

    span {
      color: #06b5a9;
    }
  }

  &__copyright {
    font-family: "HelveticaNeueRegular";
    font-weight: 400;
    color: #ffffff;
    font-size: 16px;
    line-height: 19.72px;
    opacity: 0.5;
    margin-top: 8px;
  }

  &__social {
    img {
      margin-left: 40px;
    }
  }
}