.levis {
  width: 100%;
  position: relative;
  z-index: 1;
  .wrapper();

  &__inner {
    display: flex;
    justify-content: space-between;
    margin-bottom: 70px;
  }

  &__logo {
    margin-top: -16px;
  }

  &__img {
    margin-top: 40px;
  }

  &__title {
    max-width: 550px;
    margin-top: 40px;
    margin-left: 40px;

    h2 {
      font-family: "HelveticaNeueBold";
      font-weight: 700;
      font-size: 50px;
      color: #1d1d1d;
      margin-bottom: 15px;
    }

    p {
      font-family: "HelveticaNeueRegular";
      font-weight: 400;
      font-size: 14px;
      color: #1d1d1d;
      line-height: 20px;
    }
  }

  &__block {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    margin-left: 40px;
  }

  &__price {
    font-family: "HelveticaNeueRegular";
    font-weight: 400;
    font-size: 36px;
    color: #1d1d1d;

    span {
      font-family: "HelveticaNeueBold";
      font-weight: 700;
      font-size: 36px;

    }

  }

  &__btn {
    font-family: "HelveticaNeueRegular";
    font-weight: 400;
    color: #ffffff;
    font-size: 28px;
    background-color: #d62132;
    border: none;
    padding: 5px 15px;
    cursor: pointer;
    transition: 0.2s linear;

    &:hover {
      background-color: rgba(133, 21, 32);

    }

  }

  .jeans {
    .row-flex();
    justify-content: center;

    &__title {
      font-family: "HelveticaNeueRegular";
      font-weight: 400;
      font-size: 16px;
      color: #1d1d1d;
      opacity: 0.88;
      text-align: center;
      margin-top: 10px;
    }

    &__block {
      .col();
      .size(2.4);
      min-width: 180px;
      height: 200px;
      margin-bottom: 80px;
    }

    &__img {
      position: relative;
      border: 1px solid #c5c5c5;
      height: 170px;
      transition: 0.2s linear;

      &:hover {
        border-color: #d62132;
        cursor: pointer;
      }

      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}