.intro {
  height: 780px;
  width: 100%;
  background: #31a098 url(../img/intro-bg.jpg) center no-repeat;
  background-size: cover;
  margin-top: -86px;
  position: relative;

  &__overlay {
    background-color: #31a098;
    height: 100%;
    width: 100%;
    opacity: 0.36;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }

  &__inner {
    max-width: 1280px;
    display: flex;
    justify-content: space-between;
    position: relative;
    top: 350px;
    z-index: 10;
    margin: 0 auto;
    padding: 0 20px;
  
  }

  &__block {
    padding-top: 50px;
  }

  &__btn {
    border: none;
    background: none;
    cursor: pointer;
  }

  &__subtitle {
    font-family: "HelveticaNeueRegular";
    font-weight: 400;
    font-size: 22px;
    text-transform: uppercase;
    line-height: 19.72px;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.34);
    color: #ffffff;
    letter-spacing: 29.1px;
  }

  &__title {
    color: #ffffff;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.34);
    font-family: Impact;
    font-size: 116px;
    font-weight: 400;
    text-transform: uppercase;

  }
}