.categories {
  width: 100%;
  height: 555px;
  background: #2d0e5f url(../img/categories-bg.jpg) center no-repeat;
  background-size: cover;
  position: relative;

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #2d0e5f;
    opacity: 0.36;
    z-index: 1;

  }

  &__title {
    font-family: "JosefinSlab";
    font-weight: 400;
    font-size: 116px;
    color: #ffffff;
    padding-top: 50px;
    text-align: center;
    position: relative;
    z-index: 2;
    text-transform: uppercase;
  }

  &__nav {
    position: relative;
    max-width: 1280px;
    margin: 0 auto;
    height: 300px;
    z-index: 2;
    overflow: hidden;
  }

  &__link {
    .link__bg {
      height: 199px;
      width: 199px;
      border-radius: 50%;
      position: relative;

      &--1 {
        background-color: rgba(88, 219, 211,0.5);
        position: absolute;
        top: 30px;
        left: -105px;
      }

      &--2 {
        background-color:  rgba(88, 219, 211,0.5);
        position: absolute;
        top: 0;
        left: 65px;
      }
      &--3 {
        background-color:  rgba(88, 219, 211,0.8);
        position: absolute;
        top: 60px;
        left: 215px;
      }
      &--4 {
        background-color:  rgba(88, 219, 211,0.5);
        position: absolute;
        top: 10px;
        left: 390px;
      }
      &--5 {
        background-color:  rgba(88, 219, 211,0.5);
        position: absolute;
        top: 10px;
        left: 50%;
      }
      &--6 {
        background-color:  rgba(88, 219, 211,0.8);
        position: absolute;
        top: 40px;
        left: 560px;
      }
      &--7 {
        background-color:  rgba(88, 219, 211,0.5);
        position: absolute;
        top: 60px;
        left: 720px;
      }
      &--8 {
        background-color:  rgba(88, 219, 211,0.8);
        position: absolute;
        top: 15px;
        right: 220px;
      }
      &--9 {
        background-color:  rgba(88, 219, 211,0.5);
        position: absolute;
        top: 90px;
        right: 70px;
      }
      &--10 {
        background-color: rgba(88, 219, 211,0.8);
        position: absolute;
        top: 0px;
        right: -85px;
      }
    }
    .link__icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}