.cart {
  margin-left: 117px;

  &__btn {
    border: none;
    background: none;
    display: flex;
    align-items: center;
    font-family: "HelveticaNeueRegular";
    font-weight: 400;
    font-size: 24px;
    color: #9d9d9d;
    cursor: pointer;
  }

  &__price {
    font-family: "HelveticaNeueRegular";
    font-weight: 400;
    font-size: 24px;
    color: #9d9d9d;
    margin: 0 10px;
  }

  &__icon {
    margin-right: 10px;
  }

  &__arrow {
    margin-left: 15px;
  }
}