.nav-container {
  display: flex;
  align-items: center;
}

.nav {
  ul {
    display: flex;
  }

  &__list {
    margin-left: 5px;
    position: relative;

    &--submenu:hover .submenu {
      display: block;
    }
  }

  &__link {
    font-family: "HelveticaNeueRegular";
    font-weight: 400;
    color: #626262;
    font-size: 18px;
    text-decoration: none;
    transition: 0.2s linear;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 32px;
    padding-bottom: 33px;

    &:hover {
      color: #ffffff;
      background-color: #cf1a2b;
    }
  }
}

.submenu {
  background-color: #cf1a2b;
  position: absolute;
  top: 54px;
  left: 0;
  width: 617px;
  height: 336px;
  display: none;

  &__img {
    display: flex;
    height: 245px;

    img {
      margin: auto;
    }
  }

  &__logo {
    display: block;
    font-family: "HelveticaNeueBold";
    font-weight: 700;
    font-size: 38px;
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase;
    padding-left: 35px;
    padding-top: 23px;
    padding-bottom: 18px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);

    span {
      color: #fec8cd;
    }
  }

  &__block {
    height: 100%;
    width: 217px;
  }

  ul {
    display: flex;
    flex-direction: column;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    width: 200px;

    li {
      :hover {
        color: #cf1a2b;
        background-color: #fff;
      }
    }
  }

  .ul-1 {
    position: absolute;
    top: 0;
    left: 217px;
  }

  .ul-2 {
    position: absolute;
    top: 0;
    left: 417px;
  }

  &__link {
    display: block;
    font-family: "HelveticaCE";
    font-weight: 400;
    font-size: 16px;
    color: #ffffff;
    opacity: 0.72;
    text-decoration: none;
    padding: 10px 36px;
  }
}