.discounts {
  width: 100%;
  .wrapper();

  &__blocks {
    .row-flex();
    justify-content: center;
    margin-bottom: 40px;

    .block {
      .col();
      .size(3);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: relative;
      margin-top: 40px;
      min-width: 240px;
      height: 251px;
      padding-top: 15px;
      padding-bottom: 30px;
      padding-left: 20px;
      padding-right: 20px;
      font-family: "HelveticaNeueBold";
      font-weight: 700;
      text-transform: uppercase;

      &__title {
        font-size: 30px;
        color: #ffffff;
        line-height: 32.72px;
      }

      &__subtitle {
        font-size: 24px;
        color: #ffffff;
        line-height: 19.72px;

        &--m {
          margin-top: 28px;
        }

        &--m2 {
          margin-top: 8px;
        }

        .fs-1 {
          font-size: 22px;
        }

        .fs-2 {
          font-size: 20px;
        }

        .fs-3 {
          font-size: 18px;
        }
      }

      &__price {
        font-size: 49px;
        line-height: 32.39px;
        color: #5e4c38;
      }

      img {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 10;
      }

      &--1 {
        background-color: #58dbd3;
      }

      &--2 {
        background-color: #e4ce9a;
      }

      &--3 {
        background-color: #ffa7ce;
      }

      &--4 {
        background-color: #f2ca66;
        padding: 0px;
      }

      &__btn {
        font-family: "HelveticaNeueBold";
        font-weight: 700;
        font-size: 26px;
        width: 142px;
        background-color: #fff;
        color: #f2ca66;
        border: none;
        margin-bottom: 20px;
        text-transform: uppercase;
        transition: 0.2s linear;
        cursor: pointer;
        &:hover{
          background-color: #f2ca66;
          color: #fff;
        }
      }

      &__discount {
        font-size: 106px;
        color: #fff;
        text-align: center;
        transform: scaleY(-1);
      }

      &__off {
        font-size: 62px;
        line-height: 25.32px;
        color: #fff;
        text-align: center;
      }
    }
  }
}